import React from 'react';

const HeroSection = () => {
    return (
        <section id="hero-section" className="h-screen flex flex-col items-center justify-center text-center text-white py-0 px-3">
            <div className="absolute top-0 left-0 right-0 w-full h-screen md:h-auto overflow-hidden">
                <video
                    className="w-full h-full object-cover"
                    src="/video/hero-section.mp4"
                    type="video/mp4"
                    autoPlay
                    muted
                    loop
                />
            </div>

            <div className="relative z-10 px-4 text-left md:px-8">
                <h1 className="text-2xl md:text-4xl font-bold leading-tight mb-4 w-[50%]">
                    Explore the Wonders of Our Museum
                </h1>
                <p className="text-lg mb-6 w-[50%]">
                    Experience effortless online ticket booking with instant access to art and history. Enjoy exclusive discounts, flexible scheduling, and skip-the-line convenience. Your tickets are delivered instantly to your inbox for a seamless visit. Explore special exhibitions with early access available only online. Reserve your tickets now for an unforgettable adventure!
                </p>
                <a
                    href="#book-tickets"
                    className="inline-block px-6 py-3 bg-[#FAE9A3] text-black font-semibold rounded-lg"
                >
                    Book Your Tickets Now
                </a>
            </div>
        </section>
    );
};

export default HeroSection;
